<template>
  <div class="login_container">
    <div class="login_box">
      <!--      -->
      <div class="avatar_box">
        <img src="@/assets/img/avatar.jpg" alt="">
      </div>
      <!--      -->
      <el-form ref="loginFromRef" :model="loginFrom" :rules="loginFromRules" class="e_form">
        <!--        -->
        <el-form-item prop="email">
          <el-input v-focus @keyup.enter.native="login" v-model="loginFrom.email_or_id" prefix-icon="fas fa-user"></el-input>
        </el-form-item>
        <!--        -->
        <el-form-item prop="password">
          <el-input type="password" @keyup.enter.native="login" v-model="loginFrom.password"
                    prefix-icon="fas fa-lock"></el-input>
        </el-form-item>
        <!--        -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="resetLoginFrom"></el-button>
          <el-button type="info" @click="goRegister"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  created() {
    if (!this.isNull(this.getToken())) {
      this.$router.push('./TableList')
    }
  },
  data() {
    return {
      // 
      //  rules  Form-Item  prop 
      loginFrom: {
        email_or_id: '',
        password: ''
      },
      // 
      loginFromRules: {
        email_or_id: [
          {required: true, message: '', trigger: 'blur'},
          {type: 'email', message: '', trigger: ['blur', 'change']}
        ],
        password: [
          {required: true, message: '', trigger: 'blur'}
        ]
      },
      token: ''
    }
  },
  methods: {
    goRegister() {
      this.$router.push('/Register')
    },
    // 
    resetLoginFrom() {
      this.$refs.loginFromRef.resetFields()
    },
    // 
    login() {
      // ref 
      // validate  
      this.$refs.loginFromRef.validate(async (valid) => {
        // valid 
        if (!valid) {
          this.$message.error(' ')
          this.$refs.loginFromRef.resetFields()
          return
        }

        await this.$http.post('/api/user/login', {
          id: this.loginFrom.email_or_id,
          password: this.loginFrom.password
        }).then((data) => {
          this.setToken(data.data)
          this.$message.success(data.message);
          this.$router.push('./TableList')
        })

      })
    },
  },
  // directivesv-focus
  directives: {
    focus: {
      inserted: function (el) {
        el.querySelector('input').focus()
      }
    }
  }
}
</script>

<style Lang="less" scoped>
.login_container {
  background-color: #FF7F50;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  position: relative;
}

.avatar_box {
  height: 130px;
  width: 130px;
  border: 1px solid #eee;
  border-radius: 50%;
  padding: 7px;
  box-shadow: 0 0 10px #999;
  position: absolute;
  top: -60px;
  background-color: #fff;
}

.avatar_box img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.e_form {
  width: 70%;
  margin-top: 22%;
}

.btns {
  display: flex;
  justify-content: center;
}
</style>
