import axios from 'axios'
import qs from 'qs'
import Vue from 'vue'

axios.interceptors.request.use(config => {
  // loading
  return config
}, error => {
  return Promise.reject(error)
})

axios.interceptors.response.use(response => {
  return response
}, error => {
  return Promise.resolve(error.response)
})

function checkStatus(response) {
  // loading
  // http
  if (response && (response.status === 200 || response.status === 304 || response.status === 400)) {
    return response
    // data return response.data
  }
  // 
  return {
    code: 60002,
    message: ''
  }
}

// code()
export function checkCode(data) {

  // 200
  if (data.code === 200) {
    return data
  }

  // =>  error
  throw data;
}
export function showError(data){
// 201-299
  if (201 <= data.code && data.code <= 299)
    Vue.prototype.$message.warning(data.message)

  // 1000
  else if (data.code === 1000)
    Vue.prototype.$message.error(data.message)

  //10001-19999
  else if (10001 <= data.code && data.code <= 19999)
    Vue.prototype.$message.error(data.message)

  //20001-29999
  else if (20001 <= data.code && data.code <= 29999) {
    Vue.prototype.setToken('')
    Vue.prototype.$message.error(data.message)
  }

  //30001-39999
  else if (30001 <= data.code && data.code <= 39999)
    Vue.prototype.$message.error(data.message)

  //40001-49999
  else if (40001 <= data.code && data.code <= 49999)
    Vue.prototype.$message.error(data.message)

  //50001-599999
  else if (50001 <= data.code && data.code <= 599999)
    Vue.prototype.$message.error(data.message)

  //60001-69999
  else if (60001 <= data.code && data.code <= 69999)
    Vue.prototype.$message.error(data.message)

  //70001-79999
  else if (70001 <= data.code && data.code <= 79999)
    Vue.prototype.$message.error(data.message)

  //
  else
    Vue.prototype.$message.error("")
}

//
function error(data){
  showError(data)

  throw '';
}

export default {
  post(url, data) {
    return axios({
      method: 'post',
      baseURL: axios.defaults.baseURL,
      url,
      data: qs.stringify(data),
      timeout: 10000,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
        'token': Vue.prototype.getToken()
      }
    }).then(
      (response) => {
        return checkStatus(response)
      }
    ).then(
      (res) => {
        return checkCode(res.data)
      }
    ).catch((res) => {
      error(res)
    })
  },
  get(url, params) {
    return axios({
      method: 'get',
      baseURL: axios.defaults.baseURL,
      url,
      params, // get 
      timeout: 10000,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
        'token': Vue.prototype.getToken()
      }
    }).then(
      (response) => {
        return checkStatus(response)
      }
    ).then(
      (res) => {
        return checkCode(res.data)
      }
    ).catch((res) => {
      error(res)
    })
  },
  put(url, params) {
    return axios({
      method: 'put',
      baseURL: axios.defaults.baseURL,
      url,
      params,
      timeout: 10000,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
        'token': Vue.prototype.getToken()
      }
    }).then(
      (response) => {
        return checkStatus(response)
      }
    ).then(
      (res) => {
        return checkCode(res.data)
      }
    ).catch((res) => {
      error(res)
    })
  },
  delete(url, params) {
    return axios({
      method: 'delete',
      baseURL: axios.defaults.baseURL,
      url,
      params,
      timeout: 10000,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
        'token': Vue.prototype.getToken()
      }
    }).then(
      (response) => {
        return checkStatus(response)
      }
    ).then(
      (res) => {
        return checkCode(res.data)
      }
    ).catch((res) => {
      error(res)
    })
  }
}

