<template>
  <div class="confession">
    <el-form :model="addLove" :rules="addLoveRules" ref="addLoveRef" label-width="100px">
      <h3></h3>
      <el-form-item label="" prop="CName">
        <el-input v-model="addLove.CName" placeholder=""
                  @keyup.enter.native="submitForm('addLoveRef')"></el-input>
      </el-form-item>
      <el-form-item label="" prop="CGender">
        <el-select v-model="addLove.CGender" placeholder=""
                   @keyup.enter.native="submitForm('addLoveRef')">
          <el-option label="" value=1></el-option>
          <el-option label="" value=2></el-option>
          <el-option label="" value=0></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="TA" prop="BeCName">
        <el-input v-model="addLove.BeCName" placeholder=""
                  @keyup.enter.native="submitForm('addLoveRef')"></el-input>
      </el-form-item>
      <el-form-item label="TA" prop="BeCGender">
        <el-select v-model="addLove.BeCGender" placeholder=""
                   @keyup.enter.native="submitForm('addLoveRef')">
          <el-option label="" value=1></el-option>
          <el-option label="" value=2></el-option>
          <el-option label="" value=0></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="CContent">
        <el-input type="textarea" :rows="5" v-model="addLove.CContent" placeholder="160" resize="none"></el-input>
      </el-form-item>


      <el-form-item label="" v-if="upLoad">
        <el-upload
          ref="upload"
          :action='uploadUrl'
          list-type="picture-card"
          :headers="uploadHeader"
          :on-success="submitUpload"
          :on-remove="handleRemove"
          :on-preview="handlePictureCardPreview"
          :on-exceed="handlePictureExceed"
          multiple="true"
          accept="image/jpeg,image/gif,image/png"
          limit="9">
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
      </el-form-item>
    </el-form>
    <div class="switch">
      <!--        -->
      <div class="anonymous">
        <span style="margin-right: 10px"></span>
        <el-switch v-model="addLove.anonymous"></el-switch>
      </div>
      <!--        -->
      <div class="notify_email">
        <span style="margin-right: 10px"></span>
        <el-switch v-model="notifyEmail"></el-switch>
      </div>
    </div>
    <div class="button">
      <el-button type="primary" @click="submitForm('addLoveRef')"></el-button>
      <el-button @click="resetForm('addLoveRef')"></el-button>
    </div>
  </div>
</template>

<script>
import {checkCode, showError} from '../../utils/http';
import axios from "axios";

export default {
  data() {
    return {
      dialogImageUrl: '',
      dialogVisible: false,
      disabled: false,
      uploadUrl: axios.defaults.baseURL + '/api/file/image',
      // 
      uploadHeader: {
        token: Vue.prototype.getToken(),
      },
      addLove: {
        CName: '',
        CGender: '',
        BeCName: '',
        BeCGender: '',
        CContent: '',
        anonymous: false,
        notifyEmail: false,
        //
        uploadImageMap: {}
      },
      //
      upLoad: true,
      addLoveRules: {
        CName: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            max: 6,
            message: ' 6 ',
            trigger: 'blur'
          }
        ],
        CGender: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ],
        BeCName: [
          {
            required: true,
            message: 'TA',
            trigger: 'change'
          }
        ],
        BeCGender: [
          {
            required: true,
            message: 'TA',
            trigger: 'change'
          }
        ],
        CContent: [
          {
            required: true,
            message: '',
            trigger: 'change'
          },
          {
            max: 160,
            message: ' 160 ',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  mounted() {
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (!valid) {
          return
        }

        await this.$http.post('/api/table/add', {
          sender: this.addLove.CName,
          senderSex: this.addLove.CGender,
          recipient: this.addLove.BeCName,
          recipientSex: this.addLove.BeCGender,
          content: this.addLove.CContent,
          anonymous: this.addLove.anonymous,
          notifyEmail: this.addLove.notifyEmail,
          images: JSON.stringify(Object.values(this.addLove.uploadImageMap))
        }).then((data) => {
          this.$message.success(data.message)
          this.resetForm(formName)
          this.$router.push('./TableList')
        })
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    //
    handleRemove(file, fileList) {
      delete this.addLove.uploadImageMap[file.uid];
    },
    //
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    // 
    submitUpload(data, file) {
      try {
        //
        checkCode(data)
        //
        this.addLove.uploadImageMap[file.uid] = data.data;
      } catch {
        //
        showError(data)
        //
        this.removeFile(file)
      }
    },
    //
    removeFile(file) {
      const length = this.$refs.upload.uploadFiles.length;
      for (let i = 0; i < length; i++) {
        if (this.$refs.upload.uploadFiles[i] === file) {
          this.$refs.upload.uploadFiles.splice(i, 1);
          break;
        }
      }
    },
    handlePictureExceed() {
      this.$message.warning("" + this.$refs.upload.limit + "");
    }
  }
}
</script>

<style scoped>
h3 {
  color: #4a4a4b;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #9e9e9e;
}

.el-form-item__label {
  color: #555;
}

.confession {
  width: 650px;
  height: auto;
  background-color: #fff;
  padding: 20px;
  border-radius: 20px;
}

.switch {
  margin: 20px 0;
  display: flex;
  justify-content: space-around;
}

.button {
  text-align: center;
}

</style>
